import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import update from 'immutability-helper';
import { bindActionCreators } from 'redux';
import { Link } from 'react-router-dom';
import { translate } from 'react-i18next';
import OrderBook from '../../components/orderBook/orderBook';
import Header from '../../components/header/header';
import Footer from '../../components/footer/footer';
import OrderTab from '../../components/tab/orderTab';
import connectFirebase from '../../hoc/connectFirebase';
import * as bookActions from '../../actions/book';
import * as userActions from '../../actions/user';
import * as modalActions from '../../actions/modal';
import './order.scss';

const mapStateToProps = (state) =>
{
    return {
        from: state.book.from,
        to: state.book.to,
        sell: state.book.sell,
        buy: state.book.buy,
        user: state.user.user
    };
};

const mapDispatchToProps = (dispatch) =>
{
    return {
        actions: {
            bookActions: bindActionCreators(bookActions, dispatch),
            userActions: bindActionCreators(userActions, dispatch),
            modalActions: bindActionCreators(modalActions, dispatch)
        }
    };
};

@connectFirebase
class Order extends React.Component
{
    constructor(props)
    {
        
        super();
        this.state = {
            nav: 0
        };

        this.changeNav = this.changeNav.bind(this);
    }

    changeNav(type)
    {
        this.setState(update(this.state, {
            nav: { $set: type }
        }));
    }

    render()
    {
        const { sell, buy, t, from, to } = this.props;
        const { nav } = this.state;

        return (
            <div className="co_order">
                <Header />
                <nav>
                    <span><Link to="exchange">{t('table.index')}</Link></span>
                    <img src="/img/icon/ic-breadthumb.svg" alt="arrow" />
                    <span>{t('table.order')}</span>
                </nav>
                <OrderTab switch={(type) => { this.changeNav(type); }} />
                <main>
                    <section className={nav === 0 ? 'rwd-show' : ''} >
                        <div className="title">
                            {t('table.buyOrder')}
                        </div>
                        <div className="head">
                            <div>{t('table.ratio')}({to.token})</div>
                            <div>{t('table.amount')}({from.token})</div>
                            <div>{t('table.total')}({from.token})</div>
                        </div>
                        <OrderBook book={buy} type="buy" />
                    </section>
                    <section className={nav === 1 ? 'rwd-show' : ''} >
                        <div className="title">
                            {t('table.sellOrder')}
                        </div>
                        <div className="head">
                            <div>{t('table.ratio')}({to.token})</div>
                            <div>{t('table.amount')}({from.token})</div>
                            <div>{t('table.total')}({from.token})</div>
                        </div>
                        <OrderBook book={sell} type="sell" reverse={true} />
                    </section>
                </main>
                <Footer />
            </div>
        );
    }
}

Order.defaultProps = {
    t: () => {},
};

Order.propTypes = {
    from: PropTypes.object.isRequired,
    to: PropTypes.object.isRequired,
    t: PropTypes.func,
    sell: PropTypes.array.isRequired,
    buy: PropTypes.array.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(translate()(Order));
