import React from 'react';
import PropTypes from 'prop-types';
import { translate } from 'react-i18next';

import './tab.scss';

const OrderTab = (props) =>
{
    return (
        <nav className="co_tab">
            <div className="button" onClick={() => { props.switch(0); }}>
                <span>Buy</span>
            </div>
            <div className="button" onClick={() => { props.switch(1); }}>
                <span>Sell</span>
            </div>
        </nav>
    );
};

OrderTab.defaultProps = {
    t: () => {}
};

OrderTab.propTypes = {
    switch: PropTypes.func.isRequired,
    t: PropTypes.func
};

export default translate()(OrderTab);
