/* eslint-disable no-undef */
import React from 'react';
import firebase from 'firebase/app';
import 'firebase/database';
import PropTypes from 'prop-types';

const connectFirebase = (WrapComponent) =>
{
    class ConnectFirebase extends React.Component
    {
        constructor(props)
        {
            super(props);
            this.state = {};
        }

        componentDidMount()
        {
            const { actions } = this.props;
            const { bookActions, userActions, modalActions } = actions;
            const $this = this;

            if (!firebase.apps.length)
            {
                firebase.initializeApp(finalConfig.fdb);
                window.dbObj = firebase.database();
            }

            // 初始tokenList陣列
            window.dbObj.ref('tokenList').once('value', (v) =>
            {
                if (v.val())
                {
                    bookActions.tokenListGet(v.val());
                }
            });

            // 監聽所有交易對和匯率
            window.dbObj.ref('tradeList').on('value', (v) =>
            {
                if (v.val())
                {
                    bookActions.tradeListGet(v.val());
                    // modalActions.updateLoading(true);
                    bookActions.bookGet$();
                }
            });

            // 監聽所有成交
            window.dbObj.ref('priceBooks').on('value', (v) =>
            {
                if (v.val())
                {
                    bookActions.recentTransactionGet$(Object.values(v.val()));
                }
            });

            // Deprecated: Get order book from contract
            // 監聽所有掛單
            // window.dbObj.ref('orderBooks').on('value', (v) =>
            // {
            //     const { user } = $this.props;
            //     if (v.val())
            //     {
            //         bookActions.bookGet$(Object.values(v.val()));
            //         if (user.wallet)
            //         {
            //             userActions.myBooksGet$(v);
            //         }
            //     }
            // });
        }

        componentWillUnmount()
        {
            window.dbObj.ref('tokenList').off('value');
            window.dbObj.ref('tradeList').off('value');
            window.dbObj.ref('priceBooks').off('value');
            window.dbObj.ref('orderBooks').off('value');
        }

        render()
        {
            return (
                <WrapComponent {...this.props} />
            );
        }
    }

    ConnectFirebase.propTypes = {
        tokenList: PropTypes.array.isRequired,
        actions: PropTypes.object.isRequired,
        user: PropTypes.object.isRequired,
        from: PropTypes.object.isRequired,
        to: PropTypes.object.isRequired
    };
    return ConnectFirebase;
};

export default connectFirebase;
