import React from 'react';
import PropTypes from 'prop-types';
import { translate } from 'react-i18next';
import equal from 'deep-equal';
import ReactTooltip from 'react-tooltip';

import { formatNumber, bnHex, bnTen, tenFill, buyDisplayPrice, sellDisplayPrice } from '../../utils/format';
import './orderBook.scss';

class OrderBookBuy extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {};

        this.getPrice = this.getPrice.bind(this);
    }

    shouldComponentUpdate(nextProps)
    {
        if (equal(nextProps.book, this.props.book) && equal(nextProps.t, this.props.t))
        {
            return false;
        }
        else
        {
            return true;
        }
    }

    componentDidUpdate()
    {
        const a = document.querySelector('.c_orderBook .sell > div:last-child');
        if (!this.props.reverse && this.props.type === 'sell' && a)
        {
            const ua = navigator.userAgent.toLowerCase();
            if (ua.indexOf('safari') !== -1)
            {
                a.scrollIntoView({ block: 'end' });
            }
        }
    }

    getPrice(data)
    {
        const { actions, myTransaction, to, changePrice, toastOpen, t } = this.props;

        // 如果是市價交易 不可以點擊價格
        const pendingType = document.querySelector('.pending-type .active');
        if (!pendingType || pendingType.dataset.type === '2') return;

        changePrice(data.displayPrice);

        const { myTransactionChange } = actions.userActions;
        let price = 0;

        if (myTransaction.act === data.type && myTransaction.act === 'sell')
        {
            price = data.price;
        }
        else if (myTransaction.act === data.type && myTransaction.act === 'buy')
        {
            price = data.price;
        }
        else
        {
            price = bnTen(tenFill(1, 36)).div(bnHex(data.price)).toString(16);
        }

        const total = () =>
        {
            return myTransaction.act === 'sell' ?
                bnTen(tenFill(bnHex(myTransaction.amount).mul(bnHex(price)).toString(), -18).split('.')[0]).toString(16) :
                bnTen(tenFill(bnTen(tenFill(bnHex(myTransaction.amount).toString(), 18)).div(bnHex(price)).toString(), to.decimal - 18)).toString(16);
        };


        const tt = total();
        const notValidTop = tenFill(bnHex(tt).toString(), -to.decimal) > (10 ** 9);
        const netValidBottom = tenFill(bnHex(tt).toString(), -to.decimal) < (10 ** -2);

        myTransactionChange({
            price,
            amount: myTransaction.amount,
            able: (myTransaction.amount && !notValidTop && !netValidBottom) ? true : false,
            total: myTransaction.amount ? tt : '-',
            act: this.props.myTransaction.act
        });
        if (notValidTop) return toastOpen(`Total ${to.token}${t('table.max')}${10 ** 9}`);
        if (netValidBottom) return toastOpen(`Total ${to.token}${t('table.min')}${10 ** -2}`);
    }

    renderTable()
    {
        const { book, type, t, reverse, from, to } = this.props;
        let full;
        let content = null;
        let rebook = [...book];
        if (book.length > 0)
        {
            const re = /order/;
            if (!re.test(window.location.hash))
            {
                rebook = type === 'buy' ? rebook.slice(0, 7) : rebook.reverse().slice(0, 7);
            }

            // console.log('rebook', rebook);
            if (!reverse)
            {
                rebook.sort((a, b) =>
                {
                    return b.displayPrice - a.displayPrice;
                });
            }
            else
            {
                rebook.sort((a, b) =>
                {
                    return a.displayPrice - b.displayPrice;
                });
            }

            full = rebook.reduce((max, curr) =>
            {
                return Number(curr.amount) > max ? Number(curr.amount) : max;
            }, rebook[0].amount);

            content = rebook.map((data, index) =>
            {
                const p = type === 'buy' ? buyDisplayPrice(data.price, from.decimal, to.decimal) : sellDisplayPrice(data.price, from.decimal, to.decimal);
                return (
                    <div key={data.index || index} onClick={() => { this.getPrice(data); }}>
                        <div className={type === 'buy' ? 'type green' : 'type red'}>{type === 'buy' && !reverse ? `${t('table.buy')} ${index + 1}` : `${t('table.sell')} ${reverse ? index + 1 : rebook.length - index}` }</div>
                        <div className="list">
                            <div data-tip data-for={`price${data.type}${index}`}>{data.displayPrice}</div>
                            <ReactTooltip id={`price${data.type}${index}`} place="right">{p}</ReactTooltip>

                            <div className="item" data-tip data-for={`amount${data.type}${index}`}>{formatNumber(8, Number(data.amount))}</div>
                            <ReactTooltip id={`amount${data.type}${index}`} place="right">{data.amount}</ReactTooltip>

                            <div className="item" data-tip data-for={`value${data.type}${index}`}>{formatNumber(8, Number(data.value))}</div>
                            <ReactTooltip id={`value${data.type}${index}`} place="right">{data.value}</ReactTooltip>
                            <div className={type === 'buy' ? ' percent buy' : 'percent sell'} style={{ width: `${((data.amount / full) * 100)}%` }} />
                        </div>
                    </div>
                );
            });
        }
        return (
            <div className="books">
                <div className={type === 'sell' ? 'sell order' : 'buy order'}>
                    {content}
                </div>
            </div>
        );
    }

    render()
    {
        // ReactTooltip.rebuild();
        return (
            <div className="c_orderBook">
                {this.renderTable()}
            </div>
        );
    }
}

OrderBookBuy.defaultProps = {
    t: () => {},
    reverse: false,
    actions: {},
    myTransaction: {},
    from: {},
    to: {},
    changePrice: () => {},
    toastOpen: () => {}
};

OrderBookBuy.propTypes = {
    book: PropTypes.array.isRequired,
    type: PropTypes.string.isRequired,
    t: PropTypes.func,
    reverse: PropTypes.bool,
    actions: PropTypes.object,
    myTransaction: PropTypes.object,
    from: PropTypes.object,
    to: PropTypes.object,
    changePrice: PropTypes.func,
    toastOpen: PropTypes.func,
};

export default translate()(OrderBookBuy);
